<template>
  <div class="fffbg">
    <div class="topbox header">
      <div class="logo">
        <img class="logoImg" :src="logoSrc"/>
      </div>
      <div class="topRight">
        <router-link to="/">首页</router-link>
        <router-link to="member">个人中心</router-link>
        <router-link to="shop">礼品商城</router-link>
        <router-link to="cost">资费信息</router-link>
        <router-link to="buy">一键发货</router-link>
        <router-link to="about">关于我们</router-link>
        <a  :href="'tencent://message/?uin='+qq+'&amp;Site=daifatu.com&amp;Menu=yes'" >在线客服</a>
        <!--<router-link to="member">我的后台</router-link>-->
        <div v-if="member" class="login">
        <img class="header_icon" src="../../../static/renwu.png">
        <span class="pull-left">
                  {{member.account}}，{{welcomeWord[member.vip]}}欢迎您！当前余额：
                  <span class="red">￥{{mytotal/100}}</span>
                </span>
        <span class="out" @click="out">退出账户</span>
        </div>
        <div v-else style="flex:1">
        <router-link to="login">登录</router-link>
        <router-link to="register">注册</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      member:'',
      mytotal:0,
      qq:595930841,
      logoSrc:require('../../../static/logo.png'),
      welcomeWord: ['', '尊敬的VIP', '尊敬的超级VIP'],
    };
  },
  mounted(){
    let token  = sessionStorage.getItem("token");
    if(token){
      this.getMember();
      this.getMoney();
    }
    this.getLogo();
    this.getQQ();
  },
  methods:{
    getLogo(){
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"user/me",
          "/giftmall/logo",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao.tianyalei.com",
              domain: this.url,
            }
          }
        )
        .then(response=> {
          if (response.data.code == 200&&response.data.data) {
            this.logoSrc = response.data.data
          }
        });
    },
    getQQ(){
      this.axios
        .get(
          // process.env.API_HOST+"user/me",
          "/giftmall/qq",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              domain: this.url,
            }
          }
        )
        .then(response=> {
          if (response.data.code == 200&&response.data.data) {
            this.qq = response.data.data
          }
        });
    },
    getMember(){
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"user/me",
          "/giftmall/user/me",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.member = response.data.data;
            that.$emit("member", that.member);
          }
        });
    },
    getMoney() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"wallet/myTotal",
          "/giftmall/wallet/myTotal",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,

              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function (response) {
          if (response.data.code == 200) {
            that.mytotal = response.data.data;

          }
        });
    },
    out() {
      sessionStorage.removeItem("token");
      this.$router.push({
        path: "/login"
      });
    }
  }
};
</script>

<style scoped>
  .topbox{
    margin-left:10%;
  }
.header {
  height: 80px;
  line-height: 80px;
}
.header .logo {
  height: 80px;
  float: left;
  display: flex;
  width: 200px;
}
.logoImg{
  max-width: 180px;
  margin: auto;
  max-height: 80px;
}
.topRight{
  display: flex;
  flex-direction: row;
}
.topRight a {
  text-decoration: none;
  font-size: 16px;
  margin-right: 2%;
  color: #333333;
}
.login{
  display: flex;
}
.header_icon{
  width: 30px;
  height: 30px;
  margin: auto;
}
.pull-left{
  margin-left: 15px;
  color:gray;
}
.fffbg {
  background: #fff;
}
  .out{
    margin-left: 20px;
    cursor: pointer;
    color:gray
  }
</style>



