<template>
  <div class="main_footer">
    <ul class="main_footer_root" >
      <li v-for="item in 4" style="width: 25%">
        <div style="display:flex;flex-direction: row;">
          <img class="footer_icon" :src="footImg[item-1]"/>
          <div style="display:flex;flex-direction: column;margin: 15px;
              ">
            <strong>{{footType[item-1]}}</strong>
            <span style="margin-top: 5px;color: #aaaaaa">{{footTypeDetail[item-1]}}</span>
          </div>
        </div>
      </li>
    </ul>
    <span class="company" >冀ICP备19016761号-1版权所有 石家庄彬尚商贸有限公司</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      footImg:[require('../../../static/footer_1.png'),
        require('../../../static/footer_2.png'),
        require('../../../static/footer_3.png'),
        require('../../../static/footer_4.png')],
      footType:['降成本','去库存','保品质','多选择'],
      footTypeDetail:['帮你降低50%发货成本，省去你能想到的所有发货成本',
        '回收库存/去礼品库存/去自有产品库存',
        '同样价格，给你更好的品质；同样品质，给你最低的价格',
        '非配重礼品/可配重礼品/高端礼品等'],
    };
  }
};
</script>

<style scope lang="less">
  .main_footer{
    background: white;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  .main_footer_root{
    display:flex;
    flex-direction: row;
    width:90%;
    justify-content: space-between;
    padding: 0 5%;
    border-bottom: solid 1px gainsboro
  }
  .company{
    color: #aaaaaa;
    text-align:center;
    margin: 20px auto;
  }
  .footer_icon{
    width: 50px;
    height: 50px;
    margin: 20px;
  }
  }
</style>



